"use client"

import { useState } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Badge } from '@/components/ui/badge'
import { Separator } from '@/components/ui/separator'
import { 
  Type, 
  Image, 
  Video, 
  Layout, 
  Trash2,
  Eye,
  Code,
  Save,
  Plus,
  Edit
} from 'lucide-react'
import { toast } from 'sonner'

interface PageElement {
  id: string
  type: 'text' | 'image' | 'video' | 'button' | 'section' | 'html'
  content: Record<string, any>
  styles: Record<string, string>
}

interface Page {
  id: string
  name: string
  slug: string
  elements: PageElement[]
  settings: {
    title: string
    description: string
    keywords: string
    customCSS: string
    customJS: string
  }
}

const ELEMENT_TYPES = [
  { type: 'text', label: 'Metin', icon: Type },
  { type: 'image', label: 'Görsel', icon: Image },
  { type: 'video', label: 'Video', icon: Video },
  { type: 'button', label: 'Buton', icon: Layout },
  { type: 'section', label: 'Bölüm', icon: Layout },
  { type: 'html', label: 'HTML', icon: Code }
]

const ElementRenderer = ({ element }: { element: PageElement }) => {
  switch (element.type) {
    case 'text':
      return (
        <div 
          style={element.styles}
          dangerouslySetInnerHTML={{ __html: element.content.text || 'Metin' }}
        />
      )
    case 'image':
      return (
        <img
          src={element.content.src || '/placeholder.jpg'}
          alt={element.content.alt || 'Görsel'}
          style={element.styles}
          className="max-w-full h-auto"
        />
      )
    case 'video':
      return (
        <video
          src={element.content.src}
          controls
          style={element.styles}
          className="max-w-full h-auto"
        />
      )
    case 'button':
      return (
        <button
          style={element.styles}
          className="px-4 py-2 bg-primary text-primary-foreground rounded"
        >
          {element.content.text || 'Buton'}
        </button>
      )
    case 'section':
      return (
        <div
          style={element.styles}
          className="min-h-[100px] border border-dashed border-muted-foreground/25 p-4"
        >
          {element.content.title && <h3>{element.content.title}</h3>}
          <p>{element.content.content || 'Bölüm içeriği'}</p>
        </div>
      )
    case 'html':
      return (
        <div
          dangerouslySetInnerHTML={{ __html: element.content.html || '<p>HTML içeriği</p>' }}
          style={element.styles}
        />
      )
    default:
      return <div>Bilinmeyen öğe</div>
  }
}

const ElementProperties = ({ element, onUpdate }: { element: PageElement | null, onUpdate: (element: PageElement) => void }) => {
  if (!element) {
    return (
      <div className="p-4 text-center text-muted-foreground">
        Düzenlemek için bir öğe seçin
      </div>
    )
  }

  const updateContent = (key: string, value: any) => {
    onUpdate({
      ...element,
      content: { ...element.content, [key]: value }
    })
  }

  const updateStyle = (key: string, value: string) => {
    onUpdate({
      ...element,
      styles: { ...element.styles, [key]: value }
    })
  }

  return (
    <div className="space-y-4">
      <div>
        <h3 className="font-medium mb-2">İçerik</h3>
        {element.type === 'text' && (
          <div>
            <Label htmlFor="text">Metin</Label>
            <Textarea
              id="text"
              value={element.content.text || ''}
              onChange={(e) => updateContent('text', e.target.value)}
              placeholder="Metin içeriği"
            />
          </div>
        )}
        
        {element.type === 'image' && (
          <div className="space-y-2">
            <div>
              <Label htmlFor="src">Görsel URL</Label>
              <Input
                id="src"
                value={element.content.src || ''}
                onChange={(e) => updateContent('src', e.target.value)}
                placeholder="https://example.com/image.jpg"
              />
            </div>
            <div>
              <Label htmlFor="alt">Alt Metin</Label>
              <Input
                id="alt"
                value={element.content.alt || ''}
                onChange={(e) => updateContent('alt', e.target.value)}
                placeholder="Görsel açıklaması"
              />
            </div>
          </div>
        )}
        
        {element.type === 'button' && (
          <div className="space-y-2">
            <div>
              <Label htmlFor="buttonText">Buton Metni</Label>
              <Input
                id="buttonText"
                value={element.content.text || ''}
                onChange={(e) => updateContent('text', e.target.value)}
                placeholder="Buton metni"
              />
            </div>
            <div>
              <Label htmlFor="buttonLink">Link</Label>
              <Input
                id="buttonLink"
                value={element.content.link || ''}
                onChange={(e) => updateContent('link', e.target.value)}
                placeholder="https://example.com"
              />
            </div>
          </div>
        )}
        
        {element.type === 'html' && (
          <div>
            <Label htmlFor="html">HTML Kodu</Label>
            <Textarea
              id="html"
              value={element.content.html || ''}
              onChange={(e) => updateContent('html', e.target.value)}
              placeholder="<div>HTML içeriği</div>"
              className="font-mono text-sm"
            />
          </div>
        )}
      </div>

      <Separator />

      <div>
        <h3 className="font-medium mb-2">Stil</h3>
        <div className="grid grid-cols-2 gap-2">
          <div>
            <Label htmlFor="width">Genişlik</Label>
            <Input
              id="width"
              value={element.styles.width || ''}
              onChange={(e) => updateStyle('width', e.target.value)}
              placeholder="auto"
            />
          </div>
          <div>
            <Label htmlFor="height">Yükseklik</Label>
            <Input
              id="height"
              value={element.styles.height || ''}
              onChange={(e) => updateStyle('height', e.target.value)}
              placeholder="auto"
            />
          </div>
          <div>
            <Label htmlFor="color">Metin Rengi</Label>
            <Input
              id="color"
              type="color"
              value={element.styles.color || '#000000'}
              onChange={(e) => updateStyle('color', e.target.value)}
            />
          </div>
          <div>
            <Label htmlFor="backgroundColor">Arka Plan</Label>
            <Input
              id="backgroundColor"
              type="color"
              value={element.styles.backgroundColor || '#ffffff'}
              onChange={(e) => updateStyle('backgroundColor', e.target.value)}
            />
          </div>
        </div>
      </div>
    </div>
  )
}

export default function SimplePageBuilder() {
  const [pages, setPages] = useState<Page[]>([])
  const [currentPage, setCurrentPage] = useState<Page | null>(null)
  const [selectedElement, setSelectedElement] = useState<PageElement | null>(null)
  const [previewMode, setPreviewMode] = useState(false)

  const createNewPage = () => {
    const newPage: Page = {
      id: Date.now().toString(),
      name: 'Yeni Sayfa',
      slug: 'yeni-sayfa',
      elements: [],
      settings: {
        title: 'Yeni Sayfa',
        description: '',
        keywords: '',
        customCSS: '',
        customJS: ''
      }
    }
    setPages([...pages, newPage])
    setCurrentPage(newPage)
  }

  const addElement = (elementType: string) => {
    if (!currentPage) return

    const newElement: PageElement = {
      id: Date.now().toString(),
      type: elementType as any,
      content: getDefaultContent(elementType),
      styles: getDefaultStyles(elementType)
    }

    const updatedPage = {
      ...currentPage,
      elements: [...currentPage.elements, newElement]
    }

    setCurrentPage(updatedPage)
    setPages(pages.map(p => p.id === currentPage.id ? updatedPage : p))
  }

  const updateElement = (updatedElement: PageElement) => {
    if (!currentPage) return

    const updatedPage = {
      ...currentPage,
      elements: currentPage.elements.map(el => 
        el.id === updatedElement.id ? updatedElement : el
      )
    }

    setCurrentPage(updatedPage)
    setPages(pages.map(p => p.id === currentPage.id ? updatedPage : p))
    setSelectedElement(updatedElement)
  }

  const deleteElement = (elementId: string) => {
    if (!currentPage) return

    const updatedPage = {
      ...currentPage,
      elements: currentPage.elements.filter(el => el.id !== elementId)
    }

    setCurrentPage(updatedPage)
    setPages(pages.map(p => p.id === currentPage.id ? updatedPage : p))
    setSelectedElement(null)
  }

  const savePage = async () => {
    if (!currentPage) return

    try {
      const response = await fetch('/api/admin/pages', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(currentPage)
      })

      if (response.ok) {
        toast.success('Sayfa kaydedildi!')
      }
    } catch (error) {
      toast.error('Sayfa kaydedilemedi!')
    }
  }

  return (
    <div className="h-screen flex">
      {/* Sidebar */}
      <div className="w-80 border-r bg-muted/30 flex flex-col">
        <div className="p-4 border-b">
          <div className="flex items-center justify-between mb-4">
            <h2 className="font-semibold">Sayfa Editörü</h2>
            <Button size="sm" onClick={createNewPage}>
              <Plus className="h-4 w-4 mr-1" />
              Yeni
            </Button>
          </div>
          
          {pages.length > 0 && (
            <Select value={currentPage?.id || ''} onValueChange={(id) => {
              const page = pages.find(p => p.id === id)
              setCurrentPage(page || null)
            }}>
              <SelectTrigger>
                <SelectValue placeholder="Sayfa seçin" />
              </SelectTrigger>
              <SelectContent>
                {pages.map(page => (
                  <SelectItem key={page.id} value={page.id}>
                    {page.name}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          )}
        </div>

        <Tabs defaultValue="elements" className="flex-1">
          <TabsList className="grid w-full grid-cols-2">
            <TabsTrigger value="elements">Öğeler</TabsTrigger>
            <TabsTrigger value="properties">Özellikler</TabsTrigger>
          </TabsList>
          
          <TabsContent value="elements" className="p-4 space-y-2">
            <h3 className="font-medium mb-2">Öğe Ekle</h3>
            {ELEMENT_TYPES.map(({ type, label, icon: Icon }) => (
              <Button
                key={type}
                variant="outline"
                className="w-full justify-start"
                onClick={() => addElement(type)}
              >
                <Icon className="h-4 w-4 mr-2" />
                {label}
              </Button>
            ))}
          </TabsContent>
          
          <TabsContent value="properties" className="p-4">
            <ElementProperties
              element={selectedElement}
              onUpdate={updateElement}
            />
          </TabsContent>
        </Tabs>
      </div>

      {/* Main Canvas */}
      <div className="flex-1 flex flex-col">
        <div className="border-b p-4 flex items-center justify-between">
          <div className="flex items-center space-x-2">
            {currentPage && (
              <Badge variant="outline">{currentPage.name}</Badge>
            )}
          </div>
          
          <div className="flex items-center space-x-2">
            <Button
              variant="outline"
              size="sm"
              onClick={() => setPreviewMode(!previewMode)}
            >
              <Eye className="h-4 w-4 mr-1" />
              {previewMode ? 'Düzenle' : 'Önizle'}
            </Button>
            
            <Button size="sm" onClick={savePage}>
              <Save className="h-4 w-4 mr-1" />
              Kaydet
            </Button>
          </div>
        </div>

        <div className="flex-1 p-4 overflow-auto">
          {currentPage ? (
            <div className="max-w-4xl mx-auto space-y-4">
              {currentPage.elements.map((element) => (
                <div
                  key={element.id}
                  className={`relative group border-2 ${
                    selectedElement?.id === element.id ? 'border-primary' : 'border-transparent'
                  } hover:border-primary/50 rounded p-2`}
                  onClick={() => setSelectedElement(element)}
                >
                  <ElementRenderer element={element} />
                  
                  {!previewMode && (
                    <div className="absolute top-0 right-0 opacity-0 group-hover:opacity-100 transition-opacity">
                      <div className="flex space-x-1 bg-background border rounded p-1">
                        <Button
                          size="sm"
                          variant="outline"
                          onClick={(e) => {
                            e.stopPropagation()
                            setSelectedElement(element)
                          }}
                        >
                          <Edit className="h-3 w-3" />
                        </Button>
                        <Button
                          size="sm"
                          variant="destructive"
                          onClick={(e) => {
                            e.stopPropagation()
                            deleteElement(element.id)
                          }}
                        >
                          <Trash2 className="h-3 w-3" />
                        </Button>
                      </div>
                    </div>
                  )}
                </div>
              ))}
              
              {currentPage.elements.length === 0 && (
                <div className="text-center py-12 text-muted-foreground">
                  <Layout className="h-16 w-16 mx-auto mb-4 opacity-50" />
                  <p>Sayfa boş. Sol panelden öğe ekleyin.</p>
                </div>
              )}
            </div>
          ) : (
            <div className="h-full flex items-center justify-center text-muted-foreground">
              <div className="text-center">
                <Layout className="h-16 w-16 mx-auto mb-4 opacity-50" />
                <p className="text-lg mb-2">Sayfa Editörü</p>
                <p>Başlamak için yeni bir sayfa oluşturun</p>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  )
}

function getDefaultContent(type: string) {
  switch (type) {
    case 'text':
      return { text: 'Yeni metin' }
    case 'image':
      return { src: '', alt: 'Görsel' }
    case 'video':
      return { src: '' }
    case 'button':
      return { text: 'Buton', link: '#' }
    case 'section':
      return { title: 'Bölüm Başlığı', content: 'Bölüm içeriği' }
    case 'html':
      return { html: '<p>HTML içeriği</p>' }
    default:
      return {}
  }
}

function getDefaultStyles(type: string): Record<string, string> {
  switch (type) {
    case 'text':
      return { fontSize: '16px', color: '#000000' }
    case 'image':
      return { maxWidth: '300px', height: 'auto' }
    case 'video':
      return { maxWidth: '400px', height: 'auto' }
    case 'button':
      return { 
        padding: '10px 20px', 
        backgroundColor: '#000000', 
        color: '#ffffff',
        border: 'none',
        borderRadius: '4px'
      }
    case 'section':
      return { 
        padding: '20px', 
        backgroundColor: '#f5f5f5',
        minHeight: '100px'
      }
    default:
      return {}
  }
}
